@import "nullstyle";
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.wrapper {
  overflow: hidden;
}
.main {
  &__inner {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
    max-height: 100vh;
    min-height: 100vh;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    opacity: 1 !important;
    &::after {
      font-size: 50px;
      color: #dca732;
    }
  }
  .swiper-pagination {
    &-bullet {
      width: 12px;
      height: 12px;
      background: transparent;
      border: 1px solid #dca732;
      opacity: 1 !important;
    }
    &-bullet-active {
      background: #dca732;
    }
  }
}
.table-section {
  padding: 100px 0;
  &__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
  }
  &__title {
    text-align: center;
    font-size: 80px;
    line-height: 125%;
    font-weight: 800;
    text-transform: uppercase;
  }
  &__subtitle {
    text-align: center;
    font-size: 50px;
    line-height: 125%;
    font-weight: 500;
  }
  table {
    width: 1380px;
    border-collapse: collapse;
    margin-top: 30px;
    margin-right: 30px;
    thead {
      tr th {
        border: 1px solid #F2F2F1;
        padding: 10px;
        font-size: 20px;
        line-height: 125%;
        font-weight: 700;
        &:nth-child(1) {
          width: 60px;
        }
      }  
    }
    tbody {
      tr td {
        text-align: center;
        border: 1px solid #F2F2F1;
        padding: 10px;
        font-size: 18px;
        line-height: 125%;
        font-weight: 400;
      }  
    }
  }
  .table {
    margin-top: 60px;
    &-overflow {
      width: calc(100% + 30px);
      padding-bottom: 10px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 4px;
      }
      &::-webkit-scrollbar-track {
          background: #F2F2F1;
          border-radius: 3px;
          margin-right: 30px;
      }
      &::-webkit-scrollbar-thumb {
          background: #dca732;
          border-radius: 3px;
          margin-right: 30px;
      }
    }
  }
  .table2 {
    margin-top: 60px;
  }
}
.footer {
  padding-bottom: 100px;
  .section__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .contacts_text {
    font-size: 40px;
    font-weight: 500;
    width: 80%;
  }
  .section__text {
    max-width: 650px;
  }
  .t1 {
    font-size: 30px;
  }
  .row {
    display: flex;
    flex-wrap: wrap;
    row-gap: 25px;
    margin-left: 0;
    margin-top: 60px;
    margin-left: 0;
    margin-right: 0;
    align-items: flex-start;
  }
  .contacts__info {
    max-width: calc(100% - 700px - 40px);
    padding-right: 80px;
    width: 100%;
    margin-right: auto;
    margin-top: 25px;
    box-sizing: border-box;
  }
  .contacts__list {
    padding-bottom: 78px;
    margin-bottom: 40px;
    border-bottom: 2px solid #fff;
  }
  .contacts__item {
    margin-bottom: 45px;
  }
  .contacts__item-name {
    font-size: 35px;
    margin-bottom: 40px;
    color: #bc8f4c;
    font-weight: 500;
    letter-spacing: 1px;
  }
  .contacts__item-socials a {
    font-size: 30px;
    margin-right: 40px;
    letter-spacing: 2px;
    color: #fff;
    display: block;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    font-weight: 500;
    &:hover {
      color: #bc8f4c;
    }
  }
  .contacts__item-text p {
    font-size: 30px;
    margin: 0 0 10px 0;
  }
  .contacts__item-text p:last-of-type {
    margin-bottom: 0;
  }
  .contacts__item-text a {
    font-size: 30px;
    margin-bottom: 20px;
    color: #fff;
    display: block;
    letter-spacing: 4px;
    text-transform: uppercase;
    text-decoration: none;
    outline: none;
    font-weight: 300;
    cursor: pointer;
  }
  .contacts__policy-text {
    font-size: 28px;
    margin-bottom: 65px;
    font-weight: 300;
  }
  .contacts__policy-copy {
    font-size: 32px;
    padding-left: 40px;
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
  }
  .contacts__policy-copy img {
    top: 0;
    width: 24px;
    height: 24px;
    position: absolute;
    left: 0;
    display: block;
    max-width: 100% !important;
    max-height: 100% !important;
  }
  .form {
    color: #32393F;
    background: #ECE3E9;
    border-radius: 50px;
  }
  .contacts__form {
    padding: 50px;
    max-width: 700px;
    margin-left: auto;
    box-sizing: border-box;
    width: 100%;
  }
  .form__title {
    font-size: 40px;
    margin-bottom: 80px;
    font-weight: 700;
  }
  .input {
    display: block;
    transition: 0.3s;
    position: relative;
  }
  .form__input {
    margin-bottom: 80px;
  }
  .input__area {
    width: 100%;
    color: #32393F;
    font-size: 24px;
    padding-bottom: 30px;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    transition: 0.3s;
    position: relative;
    background: transparent;
    border-bottom: 3px solid #32393F;
    border-top: 0;
    border-left: 0;
    border-right: 0;
  }
  input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
    outline: none !important;
    -webkit-tap-highlight-color: transparent;
  }
  .check {
    cursor: pointer;
    transition: 0.3s;
  }
  .form__check {
    margin-top: 100px;
  }
  .check__input {
    display: none;
  }
  .check__wrap {
    display: flex;
    flex-wrap: wrap;
    position: relative;
  }
  .check__point {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #fff;
    border: 2px solid #6E8090;
    position: relative;
    box-sizing: border-box;
  }
  .check.--checked .check__point:before {
    width: 20px;
    height: 20px;
    opacity: 1;
    box-sizing: border-box;
    visibility: visible;
  }
  .check__point:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    background: #6E8090;
    border-radius: 50%;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
    box-sizing: border-box;
  }
  .check__text {
    width: calc(100% - 70px);
    padding-left: 20px;
    color: #6E8090;
    font-size: 28px;
    line-height: 125%;
    font-weight: 500;
  }
  .form_btn {
    color: #fff;
    background: #32393F;
    min-height: 90px;
    margin-top: 50px;
    font-size: 25px;
    max-width: 370px;
    margin-left: auto;
    border-radius: 100px;
    letter-spacing: 6px;
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    overflow: hidden;
    transition: 0.3s;
    border: none;
    outline: none;
    width: 100%;
    &:hover {
      background: #2b4966;
    }
  }
  .form_btn_img {
    width: 40px;
    height: 40px;
    margin-left: 40px;
    display: block;
    max-width: 100% !important;
    max-height: 100% !important;
  }
  .contacts__policy-title {
    font-size: 35px;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 1600px) {
  .table-section {
    &__title {
      font-size: 50px;
    }
    &__subtitle {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    .section__head {
      flex-direction: column;
      align-items: start;
      gap: 30px;
    }
    .contacts_text {
      width: 100%;
    }
    .row {
      flex-direction: column;
      gap: 60px;
    }
    .contacts__info {
      max-width: 100%;
      padding: 0;
      margin: 0;
    }
    .contacts__form {
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .table-section {
    padding: 80px 0;
    &__title {
      font-size: 40px;
    }
    &__subtitle {
      font-size: 24px;
    }
    table {
      margin-right: 15px;
    }
    .table,
    .table2 {
      margin-top: 40px;
    }
    .table-overflow {
      width: calc(100% + 15px);
      &::-webkit-scrollbar-track {
        margin-right: 15px;
      }
      &::-webkit-scrollbar-thumb {
        margin-right: 15px;
      }
    }
  }
  .footer {
    padding-bottom: 80px;
    .section__head {
      gap: 20px;
    }
    .contacts_text {
      font-size: 24px;
    }
    .t1 {
      font-size: 24px;
    }
    .row {
      gap: 30px;
      margin-top: 30px;
    }
    .contacts__item-name {
      font-size: 24px;
      margin-bottom: 15px;
    }
    .contacts__item-text p {
      font-size: 18px;
    }
    .contacts__item-text a {
      font-size: 18px;
    }
    .contacts__item-socials {
      gap: 15px;
    }
    .contacts__item-socials a {
      font-size: 18px;
    }
    .contacts__list {
      padding-bottom: 30px;
      margin-bottom: 30px;
    }
    .contacts__policy-title {
      font-size: 24px;
    }
    .contacts__policy-text {
      font-size: 18px;
      margin-bottom: 30px;
    }
    .contacts__policy-copy {
      font-size: 24px;
    }
    .contacts__form {
      max-width: 100%;
      border-radius: 20px;
      padding: 30px;
    }
    .form__title {
      font-size: 24px;
      margin-bottom: 40px;
    }
    .form__input {
      margin-bottom: 40px;
    }
    .input__area {
      font-size: 18px;
    }
    .form__check {
      margin-top: 40px;
    }
    .check__text {
      font-size: 18px;
    }
    .form_btn {
      max-width: 300px;
      min-height: 60px;
      font-size: 18px;
      letter-spacing: 2px;
      margin-top: 40px;
    }
    .form_btn_img {
      margin-left: 5px;
    }
  }
}